@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: local("PT Mono"), url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: local("Roboto"), url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", "PT Mono", sans-serif;
  color: #000000;
  overflow-y: scroll;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f8fdff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #f8fdff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #f8fdff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: sticky;
  z-index: 3;
  top: 0;
  left: 0;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  color: #f2f5f7;
  min-width: 1024px;
}
@media (max-width: 1023px) {
  .header {
    position: static;
    min-width: 770px;
  }
}
@media (max-width: 767px) {
  .header {
    min-width: 320px;
  }
}

.header__wrapper {
  padding: 19px 0 13px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .header__wrapper {
    width: auto;
    padding: 8px 50px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding: 12px 25px;
  }
}

.header__nav {
  min-height: 79px;
  width: 1222px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 auto;
  padding: 0 61px 0 0;
}
@media (max-width: 1439px) {
  .header__nav {
    min-width: 961px;
    width: 85vw;
  }
}
@media (max-width: 1023px) {
  .header__nav {
    min-width: 269px;
    padding: 0;
    width: auto;
  }
}
@media (max-width: 767px) {
  .header__nav {
    min-height: 38px;
    max-width: 768px;
    min-width: 269px;
  }
}

.header__list {
  width: 502px;
  margin: 0;
  padding: 0;
  margin-left: auto;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media (max-width: 1023px) {
  .header__list {
    width: 400px;
    margin-left: auto;
    justify-content: flex-end;
    margin-right: 10px;
  }
}
@media (max-width: 767px) {
  .header__list {
    width: 224px;
  }
}

.header__item {
  min-width: 251px;
  padding-bottom: 12px;
  padding-left: 20px;
}
.header__item a {
  font-family: "PT Mono", sans-serif;
  line-height: 18px;
  color: #f2f5f7;
}
.header__item a:hover {
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .header__item {
    min-width: auto;
    width: 41px;
    height: 40px;
    margin-left: 17px;
    padding-bottom: 0;
    padding-left: 0;
  }
}

.header__button {
  cursor: pointer;
  min-width: 160px;
  min-height: 40px;
  margin-top: 15px;
  margin-bottom: auto;
  margin-left: auto;
  padding: 12px 15px;
  color: #f2f5f7;
  text-transform: uppercase;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
.header__button:hover {
  color: #00b2ff;
  border: 1px solid #00b2ff;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.header__item-mail {
  position: relative;
}
.header__item-mail a {
  padding-left: 30px;
}
.header__item-mail a::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("../img/svg/mail.svg");
  background-repeat: no-repeat;
  left: 14px;
  bottom: 11px;
}
@media (max-width: 1023px) {
  .header__item-mail a span {
    display: none;
    padding-left: 0;
  }
  .header__item-mail a::before {
    left: 0;
    bottom: 0;
    top: 3px;
  }
}
@media (max-width: 767px) {
  .header__item-mail {
    left: 9px;
    top: 5px;
  }
}

.header__item-phone {
  position: relative;
}
.header__item-phone a {
  padding-left: 30px;
}
.header__item-phone a::before {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  background-image: url("../img/svg/phone.svg");
  background-repeat: no-repeat;
  left: 14px;
  bottom: 11px;
}
@media (max-width: 1023px) {
  .header__item-phone a span {
    display: none;
    padding-left: 0;
  }
  .header__item-phone a::before {
    left: 0;
    bottom: 0;
    top: 3px;
  }
}
@media (max-width: 767px) {
  .header__item-phone {
    left: 9px;
    top: 5px;
  }
}

.header__logo-desktop {
  margin-top: 12px;
  margin-bottom: auto;
}
@media (max-width: 767px) {
  .header__logo-desktop {
    display: none;
  }
}

.header__logo-mobile {
  display: none;
}
@media (max-width: 767px) {
  .header__logo-mobile {
    display: block;
  }
}

.footer {
  width: 100%;
  min-height: 280px;
  background: #041827;
}
@media (max-width: 1023px) {
  .footer {
    min-height: 465px;
  }
}
@media (max-width: 767px) {
  .footer {
    min-height: 0;
  }
}

.footer__inner {
  max-width: 1220px;
  min-height: 280px;
  padding: 44px 0 0;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin: 0 auto;
}
@media (max-width: 1279px) {
