  content: "";
  position: absolute;
  width: 27px;
  height: 2px;
  transform: rotate(45deg);
  background-color: #f8fdff;
  right: 6px;
  top: 17px;
}
.modal-feedback .modal-feedback__button-close::after {
  content: "";
  position: absolute;
  width: 27px;
  height: 2px;
  transform: rotate(-45deg);
  background-color: #f8fdff;
  right: 6px;
  top: 17px;
}
